.details{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 30px;
}
.details img{
    /* max-width: 400px;
    min-width: 200px; */
    width: 100%;
    height: 100%;
    padding: 5px;
    display: block;
    object-fit: cover;
    /* margin: 10px; */
    /* cursor: pointer; */
}
.details .box{
    max-width: 500px;
    min-width: 150x;
    margin: 20px;
    box-shadow: 2px 8px 20px #ddd;
}
.box .row{
    display: flex;
    justify-content: flex-start;
    margin-bottom: 10px;
}
.box .row h2{
    text-transform: uppercase;
    letter-spacing: 2px;
}
.box .row span{
    color: crimson;
}
.box .colors button{
    width: 30px;
    height: 30px;
    border: 1px solid #333;
    outline: none;
    margin-right: 5px;
    cursor: pointer;
}
.box p{
    line-height: 1.5;
    margin: 15px 0;
}
.box .cart{
    background: #333;
    color: white;
    text-decoration: none;
    padding: 10px 25px;
    display: inline-block;
}